import { ApiProperty } from '@nestjs/swagger';
import { IsIn, IsOptional } from 'class-validator';
import { IsNotBlankString } from '../../../validators/is-not-blank-string.validator';
import { ProjectStatus, projectStatusList } from '@hrms/shared/models';
import { IUpdateProjectDto } from '@hrms/shared/dtos';

export class UpdateProjectDto implements IUpdateProjectDto {
  @ApiProperty({ example: 'Time Tracker' })
  @IsOptional()
  @IsNotBlankString()
  public readonly name?: string;

  @ApiProperty({
    enum: projectStatusList,
    example: 'active',
    required: false,
  })
  @IsOptional()
  @IsIn(projectStatusList)
  public readonly status?: ProjectStatus;
}
