import { Environment, environmentTypeList } from '@hrms/shared/models';
import { ApiPropertyOptional } from '@nestjs/swagger';
import { IsIn, IsOptional } from 'class-validator';

export class GetFeatureFlagsQueryParams {
  @ApiPropertyOptional({
    enum: environmentTypeList,
    example: 'dev',
  })
  @IsOptional()
  @IsIn(environmentTypeList)
  public env?: Environment;
}
