import { ApiProperty } from '@nestjs/swagger';
import { IsBoolean, IsIn } from 'class-validator';
import { ICreateFeatureFlagDto } from '@hrms/shared/dtos';
import { Environment, environmentTypeList } from '@hrms/shared/models';
import { IsNotBlankString } from '../../../validators/is-not-blank-string.validator';

export class CreateFeatureFlagDto implements ICreateFeatureFlagDto {
  @ApiProperty({ example: 'myFeatureFlag' })
  @IsNotBlankString()
  public readonly key: string;

  @ApiProperty({ example: true })
  @IsBoolean()
  public readonly value: boolean;

  @ApiProperty({
    enum: environmentTypeList,
    example: 'dev',
  })
  @IsIn(environmentTypeList)
  public readonly environment: Environment;
}
