import { Environment } from '@hrms/shared/models';
import { Schema, Prop, SchemaFactory } from '@nestjs/mongoose';
import { HydratedDocument, Types } from 'mongoose';

export type FeatureFlagDocument = HydratedDocument<FeatureFlagRecord>;

@Schema({ collection: 'featureFlags' })
export class FeatureFlagRecord {
  public _id: Types.ObjectId;

  @Prop({ required: true })
  public key: string;

  @Prop({ required: true })
  public value: boolean;

  @Prop({ required: true, type: String })
  public environment: Environment;
}

const FeatureFlagSchema = SchemaFactory.createForClass(FeatureFlagRecord);

export { FeatureFlagSchema };
