export const timesheetEventTypeList = ['work', 'sick', 'vacation'] as const;
export type TimesheetEventType = (typeof timesheetEventTypeList)[number];

export interface ITimesheetEventContext {
  activityId: string;
  projectId?: string;
  clientId?: string;
}

export interface ITimesheetEvent {
  id: string;
  userId: string;
  date: string;
  durationMinutes: number;
  eventType: TimesheetEventType;
  comment?: string;
  context?: ITimesheetEventContext;
}
