import { Module } from '@nestjs/common';
import { MongooseModule } from '@nestjs/mongoose';

import { UserController } from './user.controller';
import { UsersService } from '../users/users.service';
import { UsersAssembler } from '../users/users.assembler';
import { UserRecordHelper } from '../users/helpers/user-record.helper';
import { ClientsService } from '../clients/clients.service';
import { ProjectsService } from '../projects/projects.service';
import { ActivitiesService } from '../activities/activities.service';
import { TimesheetEventsService } from '../timesheet-events/timesheet-events.service';
import { TimesheetEventsAssembler } from '../timesheet-events/timesheet-events.assembler';
import { WorkSummariesService } from '../work-summaries/work-summaries.service';
import { WorkSummariesAssembler } from '../work-summaries/work-summaries.assembler';

import { UserRecord, UserSchema } from '../users/schemas/user.schema';
import { ClientRecord, ClientSchema } from '../clients/schemas/client.schema';
import {
  ProjectRecord,
  ProjectSchema,
} from '../projects/schemas/project.schema';
import {
  ActivityRecord,
  ActivitySchema,
} from '../activities/schemas/activity.schema';
import {
  TimesheetEventRecord,
  TimesheetEventSchema,
} from '../timesheet-events/schemas/timesheet-event.schema';
import {
  WorkSummaryRecord,
  WorkSummarySchema,
} from '../work-summaries/schemas/work-summary.schema';

@Module({
  controllers: [UserController],
  providers: [
    UsersService,
    UsersAssembler,
    UserRecordHelper,
    ClientsService,
    ProjectsService,
    ActivitiesService,
    TimesheetEventsService,
    TimesheetEventsAssembler,
    WorkSummariesService,
    WorkSummariesAssembler,
  ],
  imports: [
    MongooseModule.forFeature([{ name: UserRecord.name, schema: UserSchema }]),
    MongooseModule.forFeature([
      { name: ClientRecord.name, schema: ClientSchema },
    ]),
    MongooseModule.forFeature([
      { name: ProjectRecord.name, schema: ProjectSchema },
    ]),
    MongooseModule.forFeature([
      { name: ActivityRecord.name, schema: ActivitySchema },
    ]),
    MongooseModule.forFeature([
      { name: TimesheetEventRecord.name, schema: TimesheetEventSchema },
    ]),
    MongooseModule.forFeature([
      { name: WorkSummaryRecord.name, schema: WorkSummarySchema },
    ]),
  ],
})
export class UserModule {}
