import { Module } from '@nestjs/common';
import { MongooseModule } from '@nestjs/mongoose';
import { UserRecord, UserSchema } from './schemas/user.schema';
import { UsersController } from './users.controller';
import { UsersService } from './users.service';
import { UsersAssembler } from './users.assembler';
import { UserRecordHelper } from './helpers/user-record.helper';
import { AuthModule } from '../auth/auth.module';
import { ProjectsModule } from '../projects/projects.module';
import { ActivitiesModule } from '../activities/activities.module';

@Module({
  exports: [UsersService],
  controllers: [UsersController],
  providers: [UsersService, UsersAssembler, UserRecordHelper],
  imports: [
    MongooseModule.forFeature([{ name: UserRecord.name, schema: UserSchema }]),
    AuthModule,
    ProjectsModule,
    ActivitiesModule,
  ],
})
export class UsersModule {}
