import { Controller, Get, Query, Req } from '@nestjs/common';
import {
  ApiBadRequestResponse,
  ApiBearerAuth,
  ApiForbiddenResponse,
  ApiInternalServerErrorResponse,
  ApiOkResponse,
  ApiTags,
  ApiUnauthorizedResponse,
} from '@nestjs/swagger';
import { TimesheetSummary } from '../timesheet-summaries/models/timesheet-summary';
import { GetTimesheetSummariesQueryParams } from '../timesheet-summaries/query-params/get-timesheet-summaries.query-params';
import { TimesheetSummariesService } from '../timesheet-summaries/timesheet-summaries.service';
import { AuthService } from '../auth/auth.service';

@ApiTags('Reports')
@Controller('reports')
export class ReportsController {
  constructor(
    private timesheetSummariesService: TimesheetSummariesService,
    private authService: AuthService
  ) {}

  @ApiBearerAuth()
  @ApiOkResponse({
    description: 'Success',
    type: TimesheetSummary,
    isArray: true,
  })
  @ApiBadRequestResponse({ description: 'Bad request' })
  @ApiUnauthorizedResponse({ description: 'Unauthorized' })
  @ApiForbiddenResponse({ description: 'Forbidden' })
  @ApiInternalServerErrorResponse({ description: 'Internal error' })
  @Get('timesheet-summaries')
  public async getTimesheetSummaries(
    @Req() request: Request,
    @Query()
    {
      startDate,
      endDate,
      clientId,
      projectId,
    }: GetTimesheetSummariesQueryParams
  ): Promise<TimesheetSummary[]> {
    await this.authService.ensureCurrentUserIsAdmin(request);

    const timesheetSummaries =
      await this.timesheetSummariesService.getTimesheetSummaries(
        startDate,
        endDate,
        clientId,
        projectId
      );

    await this.timesheetSummariesService.fillTimesheetSummariesForAllUsers(
      timesheetSummaries,
      clientId,
      projectId
    );

    return timesheetSummaries;
  }
}
