import {
  Body,
  Controller,
  Delete,
  Get,
  HttpCode,
  HttpStatus,
  Param,
  Patch,
  Post,
  Query,
  Req,
} from '@nestjs/common';
import {
  ApiBadRequestResponse,
  ApiBearerAuth,
  ApiConflictResponse,
  ApiCreatedResponse,
  ApiForbiddenResponse,
  ApiInternalServerErrorResponse,
  ApiNoContentResponse,
  ApiNotFoundResponse,
  ApiOkResponse,
  ApiParam,
  ApiTags,
  ApiUnauthorizedResponse,
} from '@nestjs/swagger';
import { FeatureFlag } from './models/feature-flag';
import { GetFeatureFlagsQueryParams } from './query-params/get-feature-flags.query-params';
import { FeatureFlagsService } from './feature-flags.service';
import { SkipAuth } from '../auth/skip-auth.decorator';
import { ICreatedIdDto } from '@hrms/shared/dtos';
import { CreateFeatureFlagDto } from './dtos/create-feature-flag.dto';
import { UpdateFeatureFlagDto } from './dtos/update-feature-flag.dto';
import { UpdateFeatureFlagParams } from './params/update-feature-flag.params';
import { AuthService } from '../auth/auth.service';
import { DeleteFeatureFlagParams } from './params/delete-feature-flag.params';

@ApiTags('Feature flags')
@Controller('feature-flags')
export class FeatureFlagsController {
  constructor(
    private featureFlagsService: FeatureFlagsService,
    private authService: AuthService
  ) {}

  @ApiOkResponse({
    description: 'Success',
    type: FeatureFlag,
    isArray: true,
  })
  @ApiInternalServerErrorResponse({ description: 'Internal error' })
  @SkipAuth()
  @Get()
  public async getFeatureFlags(
    @Query()
    { env: environment }: GetFeatureFlagsQueryParams
  ): Promise<FeatureFlag[]> {
    const featureFlags = await this.featureFlagsService.getFeatureFlags(
      environment
    );

    return featureFlags.map(({ _id, key, value, environment }) => ({
      id: _id.toString(),
      key,
      value,
      environment,
    }));
  }

  @ApiCreatedResponse()
  @ApiBearerAuth()
  @ApiBadRequestResponse({ description: 'Bad request' })
  @ApiUnauthorizedResponse({ description: 'Unauthorized' })
  @ApiForbiddenResponse({ description: "User's role is not 'admin'" })
  @ApiConflictResponse({ description: 'Key already exists for environment' })
  @ApiInternalServerErrorResponse({ description: 'Internal error' })
  @Post()
  public async createFeatureFlag(
    @Req() request,
    @Body()
    { key, value, environment }: CreateFeatureFlagDto
  ): Promise<ICreatedIdDto> {
    await this.authService.ensureCurrentUserIsAdmin(request);

    const id = await this.featureFlagsService.createFeatureFlag(
      key,
      value,
      environment
    );
    return { id };
  }

  @ApiBearerAuth()
  @ApiNoContentResponse({ description: 'No content' })
  @ApiBadRequestResponse({ description: 'Bad request' })
  @ApiUnauthorizedResponse({ description: 'Unauthorized' })
  @ApiForbiddenResponse({ description: "User's role is not 'admin'" })
  @ApiNotFoundResponse({ description: 'Feature flag id not found' })
  @ApiInternalServerErrorResponse({ description: 'Internal error' })
  @ApiParam({ required: true, name: 'featureFlagId' })
  @HttpCode(HttpStatus.NO_CONTENT)
  @Patch(':featureFlagId')
  public async updateFeatureFlag(
    @Req() request,
    @Param() { featureFlagId }: UpdateFeatureFlagParams,
    @Body()
    { value }: UpdateFeatureFlagDto
  ): Promise<void> {
    await this.authService.ensureCurrentUserIsAdmin(request);

    await this.featureFlagsService.updateFeatureFlag(featureFlagId, value);
  }

  @ApiBearerAuth()
  @ApiNoContentResponse({ description: 'No content' })
  @ApiBadRequestResponse({ description: 'Bad request' })
  @ApiUnauthorizedResponse({ description: 'Unauthorized' })
  @ApiForbiddenResponse({ description: "User's role is not 'admin'" })
  @ApiNotFoundResponse({ description: 'Feature flag id not found' })
  @ApiInternalServerErrorResponse({ description: 'Internal error' })
  @ApiParam({ required: true, name: 'featureFlagId' })
  @HttpCode(HttpStatus.NO_CONTENT)
  @Delete(':featureFlagId')
  public async deleteFeatureFlag(
    @Req() request,
    @Param() { featureFlagId }: DeleteFeatureFlagParams
  ): Promise<void> {
    await this.authService.ensureCurrentUserIsAdmin(request);

    await this.featureFlagsService.deleteFeatureFlag(featureFlagId);
  }
}
