import { Injectable } from '@nestjs/common';
import { InjectModel } from '@nestjs/mongoose';
import { Model } from 'mongoose';

import { UsersService } from '../users/users.service';
import { TimesheetEventRecord } from '../timesheet-events/schemas/timesheet-event.schema';
import { WorkSummaryRecord } from '../work-summaries/schemas/work-summary.schema';
import { DateUtils } from '../../validators/date-utils.validator';
import { TimesheetSummary } from './models/timesheet-summary';

@Injectable()
export class TimesheetSummariesService {
  constructor(
    private usersService: UsersService,
    @InjectModel(TimesheetEventRecord.name)
    private timesheetEventModel: Model<TimesheetEventRecord>,
    @InjectModel(WorkSummaryRecord.name)
    private workSummaryModel: Model<WorkSummaryRecord>
  ) {}

  public async fillTimesheetSummariesForAllUsers(
    timesheetSummaries: TimesheetSummary[],
    clientId?: string,
    projectId?: string
  ): Promise<void> {
    const allUsers = await this.usersService.findAll();

    allUsers.forEach((user) => {
      const userId = user._id.toString();

      const foundSummary = timesheetSummaries.find(
        (timesheetSummary) => timesheetSummary.userId === userId
      );

      if (foundSummary) {
        return;
      }

      if (clientId) {
        const assignedClient = user.clients.find(
          (client) => client._id.toString() === clientId
        );

        if (!assignedClient) {
          return;
        }
      }

      if (projectId) {
        const assignedProject = user.projects.find(
          (project) => project._id.toString() === projectId
        );

        if (!assignedProject) {
          return;
        }
      }

      const newSummary: TimesheetSummary = {
        userId,
        userFirstName: user.firstName,
        userLastName: user.lastName,
        durationMinutes: 0,
        summaryText: [],
      };

      timesheetSummaries.push(newSummary);
    });
  }

  public async getTimesheetSummaries(
    startDate: string,
    endDate: string,
    clientId?: string,
    projectId?: string
  ): Promise<TimesheetSummary[]> {
    DateUtils.validateDateRange(startDate, endDate);

    const timesheetSummaries: TimesheetSummary[] = [];

    const timesheetEventRecords = await this.getTimesheetEventRecords(
      startDate,
      endDate,
      clientId,
      projectId
    );
    const workSummaryRecords = await this.getWorkSummaryRecords(
      startDate,
      endDate,
      clientId,
      projectId
    );

    this.setSummariesForTimesheetEvents(
      timesheetEventRecords,
      timesheetSummaries
    );
    this.setSummariesForWorkSummaries(workSummaryRecords, timesheetSummaries);

    return timesheetSummaries;
  }

  private async getTimesheetEventRecords(
    startDate: string,
    endDate: string,
    clientId?: string,
    projectId?: string
  ): Promise<TimesheetEventRecord[]> {
    const query: Record<string, unknown> = {
      date: {
        $gte: startDate,
        $lte: endDate,
      },
    };

    if (clientId || projectId) {
      const conditions: Record<string, string> = {};

      if (clientId) {
        conditions.client = clientId;
      }
      if (projectId) {
        conditions.project = projectId;
      }

      query.$or = [conditions, { eventType: ['sick', 'vacation'] }];
    }

    let timesheetEventRecords = await this.timesheetEventModel
      .find(query)
      .populate('user')
      .exec();

    timesheetEventRecords = timesheetEventRecords.filter((record) => {
      if (record.eventType === 'work') {
        return true;
      }

      if (clientId) {
        const assignedClient = record.user.clients.find(
          (client) => client._id.toString() === clientId
        );

        if (!assignedClient) {
          return false;
        }
      }

      if (projectId) {
        const assignedProject = record.user.projects.find(
          (project) => project._id.toString() === projectId
        );

        if (!assignedProject) {
          return false;
        }
      }

      return true;
    });

    return timesheetEventRecords;
  }

  private setSummariesForTimesheetEvents(
    timesheetEventRecords: TimesheetEventRecord[],
    timesheetSummaries: TimesheetSummary[]
  ): void {
    timesheetEventRecords.forEach((record: TimesheetEventRecord) => {
      const recordUserId = record.user._id.toString();
      const recordClientId = record.client?._id.toString();
      const recordProjectId = record.project?._id.toString();

      const foundSummary = timesheetSummaries.find(
        (timesheetSummary) =>
          timesheetSummary.userId === recordUserId &&
          timesheetSummary.eventType === record.eventType &&
          timesheetSummary.context?.clientId === recordClientId &&
          timesheetSummary.context?.projectId === recordProjectId
      );

      if (foundSummary) {
        foundSummary.durationMinutes += record.durationMinutes;
      } else {
        const newSummary: TimesheetSummary = {
          userId: recordUserId,
          userFirstName: record.user.firstName,
          userLastName: record.user.lastName,
          durationMinutes: record.durationMinutes,
          eventType: record.eventType,
          summaryText: [],
        };

        if (record.eventType === 'work') {
          newSummary.context = {
            clientId: recordClientId || '',
            projectId: recordProjectId || '',
          };
        }

        timesheetSummaries.push(newSummary);
      }
    });
  }

  private async getWorkSummaryRecords(
    startDate: string,
    endDate: string,
    clientId?: string,
    projectId?: string
  ): Promise<WorkSummaryRecord[]> {
    const query: Record<string, unknown> = {
      date: {
        $gte: startDate,
        $lte: endDate,
      },
    };

    if (clientId) {
      query.client = clientId;
    }

    if (projectId) {
      query.project = projectId;
    }

    const workSummaryRecords = await this.workSummaryModel
      .find(query)
      .populate('user')
      .exec();

    return workSummaryRecords;
  }

  private setSummariesForWorkSummaries(
    workSummaryRecords: WorkSummaryRecord[],
    timesheetSummaries: TimesheetSummary[]
  ): void {
    workSummaryRecords.forEach((record: WorkSummaryRecord) => {
      const recordUserId = record.user._id.toString();
      const recordClientId = record.client._id.toString();
      const recordProjectId = record.project._id.toString();

      const foundSummary = timesheetSummaries.find(
        (timesheetSummary) =>
          timesheetSummary.userId === recordUserId &&
          timesheetSummary.eventType === 'work' &&
          timesheetSummary.context?.clientId === recordClientId &&
          timesheetSummary.context?.projectId === recordProjectId
      );

      if (foundSummary) {
        foundSummary.summaryText.push(record.summary);
      } else {
        const newSummary: TimesheetSummary = {
          userId: recordUserId,
          userFirstName: record.user.firstName,
          userLastName: record.user.lastName,
          durationMinutes: 0,
          eventType: 'work',
          summaryText: [record.summary],
          context: {
            clientId: recordClientId || '',
            projectId: recordProjectId || '',
          },
        };

        timesheetSummaries.push(newSummary);
      }
    });
  }
}
