import { Schema, Prop, SchemaFactory } from '@nestjs/mongoose';
import { HydratedDocument, SchemaTypes, Types } from 'mongoose';
import { ClientRecord } from '../../clients/schemas/client.schema';
import { ProjectStatus } from '@hrms/shared/models';

export type ProjectDocument = HydratedDocument<ProjectRecord>;

@Schema({ collection: 'projects' })
export class ProjectRecord {
  public _id: Types.ObjectId;

  @Prop({ type: SchemaTypes.ObjectId, ref: ClientRecord.name, required: true })
  public client: ClientRecord;

  @Prop({ required: true })
  public name: string;

  @Prop({ required: true, default: 'active', type: String })
  public status: ProjectStatus;
}

const ProjectSchema = SchemaFactory.createForClass(ProjectRecord);

export { ProjectSchema };
