import { ApiProperty } from '@nestjs/swagger';
import { IsIn } from 'class-validator';
import { UserStatus, userStatusList } from '@hrms/shared/models';
import { IUpdateUserStatusDto } from '@hrms/shared/dtos';

export class UpdateUserStatusDto implements IUpdateUserStatusDto {
  @ApiProperty({
    enum: userStatusList,
    example: 'enabled',
  })
  @IsIn(userStatusList)
  public readonly status: UserStatus;
}
