import {
  ConflictException,
  Injectable,
  InternalServerErrorException,
  NotFoundException,
} from '@nestjs/common';
import { InjectModel } from '@nestjs/mongoose';
import { FeatureFlagRecord } from './schemas/feature-flag.schema';
import { Environment } from '@hrms/shared/models';
import { Model } from 'mongoose';

@Injectable()
export class FeatureFlagsService {
  constructor(
    @InjectModel(FeatureFlagRecord.name)
    private featureFlagModel: Model<FeatureFlagRecord>
  ) {}

  public async getFeatureFlags(
    environment?: Environment
  ): Promise<FeatureFlagRecord[]> {
    const featureFlags = await this.featureFlagModel
      .find(environment ? { environment } : {})
      .exec();

    return featureFlags;
  }

  public async createFeatureFlag(
    key: string,
    value: boolean,
    environment: Environment
  ): Promise<string> {
    const trimmedKey = key.trim();

    const existingFeatureFlag = await this.findFeatureFlagByKey(
      trimmedKey,
      environment
    );
    if (existingFeatureFlag) {
      throw new ConflictException(
        `Key '${trimmedKey}' already exists for environment '${environment}'`
      );
    }

    const featureFlagsModel = new this.featureFlagModel({
      key: trimmedKey,
      value,
      environment,
    });

    const featureFlagId = (await featureFlagsModel.save())?.id;
    if (!featureFlagId) {
      throw new InternalServerErrorException('Failed to save feature flag');
    }

    return featureFlagId;
  }

  public async updateFeatureFlag(
    featureFlagId: string,
    value: boolean | undefined
  ): Promise<void> {
    const featureFlag = await this.featureFlagModel.findById(featureFlagId);

    if (!featureFlag) {
      throw new NotFoundException(
        `Feature flag with id '${featureFlagId}' not found`
      );
    }

    if (value !== undefined) {
      await featureFlag.updateOne({ value });
    }
  }

  public async deleteFeatureFlag(featureFlagId: string): Promise<void> {
    const featureFlag = await this.featureFlagModel.findById(featureFlagId);

    if (!featureFlag) {
      throw new NotFoundException(
        `Feature flag with id '${featureFlagId}' not found`
      );
    }

    await featureFlag.deleteOne();
  }

  private findFeatureFlagByKey(
    trimmedKey: string,
    environment: Environment
  ): Promise<FeatureFlagRecord | null> {
    const featureFlagKeyRegex = '^' + trimmedKey + '$';

    return this.featureFlagModel.findOne({
      key: { $regex: featureFlagKeyRegex, $options: 'i' },
      environment,
    });
  }
}
