import { Schema, Prop, SchemaFactory } from '@nestjs/mongoose';
import { HydratedDocument, SchemaTypes, Types } from 'mongoose';
import { ClientRecord } from '../../clients/schemas/client.schema';
import { ProjectRecord } from '../../projects/schemas/project.schema';
import { UserRecord } from '../../users/schemas/user.schema';

export type WorkSummaryDocument = HydratedDocument<WorkSummaryRecord>;

@Schema({ collection: 'workSummaries' })
export class WorkSummaryRecord {
  public _id: Types.ObjectId;

  @Prop({ type: SchemaTypes.ObjectId, ref: UserRecord.name, required: true })
  public user: UserRecord;

  @Prop({ type: SchemaTypes.ObjectId, ref: ClientRecord.name, required: true })
  public client: ClientRecord;

  @Prop({ type: SchemaTypes.ObjectId, ref: ProjectRecord.name, required: true })
  public project: ProjectRecord;

  @Prop({ required: true })
  public date: string;

  @Prop({ required: true })
  public summary: string;

  @Prop({ required: false })
  public isSREDEligible?: boolean;
}

const WorkSummarySchema = SchemaFactory.createForClass(WorkSummaryRecord);

export { WorkSummarySchema };
