import { ApiProperty } from '@nestjs/swagger';
import { ITimesheetSummary, TimesheetEventType } from '@hrms/shared/models';

export class TimesheetSummary implements ITimesheetSummary {
  @ApiProperty()
  public userId: string;

  @ApiProperty()
  public userFirstName: string;

  @ApiProperty()
  public userLastName: string;

  @ApiProperty()
  public durationMinutes: number;

  @ApiProperty()
  public summaryText: string[]; // all work summaries within the specified time range

  @ApiProperty()
  public eventType?: TimesheetEventType;

  @ApiProperty()
  public context?: {
    projectId: string;
    clientId: string;
  };
}
