import {
  ITimesheetEvent,
  ITimesheetEventContext,
  TimesheetEventType,
} from '@hrms/shared/models';
import { ApiProperty } from '@nestjs/swagger';

export class TimesheetEventContext implements ITimesheetEventContext {
  @ApiProperty()
  public activityId: string;

  @ApiProperty()
  public projectId?: string;

  @ApiProperty()
  public clientId?: string;
}

export class TimesheetEvent implements ITimesheetEvent {
  @ApiProperty()
  public id: string;

  @ApiProperty()
  public userId: string;

  @ApiProperty()
  public date: string;

  @ApiProperty()
  public durationMinutes: number;

  @ApiProperty()
  public eventType: TimesheetEventType;

  @ApiProperty()
  public comment?: string;

  @ApiProperty()
  public context?: TimesheetEventContext;
}
