import { Schema, Prop, SchemaFactory } from '@nestjs/mongoose';
import { HydratedDocument, SchemaTypes, Types } from 'mongoose';
import { UserRecord } from '../../users/schemas/user.schema';
import { ActivityRecord } from '../../activities/schemas/activity.schema';
import { TimesheetEventType } from '@hrms/shared/models';
import { ProjectRecord } from '../../projects/schemas/project.schema';
import { ClientRecord } from '../../clients/schemas/client.schema';

export type TimesheetEventDocument = HydratedDocument<TimesheetEventRecord>;

@Schema({ collection: 'timesheetEvents' })
export class TimesheetEventRecord {
  public _id: Types.ObjectId;

  @Prop({ type: SchemaTypes.ObjectId, ref: UserRecord.name, required: true })
  public user: UserRecord;

  @Prop({ required: true })
  public date: string;

  @Prop({ required: true })
  public durationMinutes: number;

  @Prop({ required: true, default: 'work', type: String })
  public eventType: TimesheetEventType;

  @Prop({ required: false })
  public comment?: string;

  @Prop({
    type: SchemaTypes.ObjectId,
    ref: ActivityRecord.name,
    required: false,
  })
  public activity?: ActivityRecord;

  @Prop({
    type: SchemaTypes.ObjectId,
    ref: ProjectRecord.name,
    required: false,
  })
  public project?: ProjectRecord;

  @Prop({
    type: SchemaTypes.ObjectId,
    ref: ClientRecord.name,
    required: false,
  })
  public client?: ClientRecord;
}

const TimesheetEventSchema = SchemaFactory.createForClass(TimesheetEventRecord);

export { TimesheetEventSchema };
