import { ClientContactMethod, ClientStatus } from '@hrms/shared/models';
import { Schema, Prop, SchemaFactory } from '@nestjs/mongoose';
import { HydratedDocument, Types } from 'mongoose';

export type ClientDocument = HydratedDocument<ClientRecord>;

@Schema({ collection: 'clients' })
export class ClientRecord {
  public _id: Types.ObjectId;

  @Prop({ required: true })
  public name: string;

  @Prop({ required: true, default: 'active', type: String })
  public status: ClientStatus;

  @Prop({ required: false })
  public contactName?: string;

  @Prop({ required: false })
  public contactEmail?: string;

  @Prop({ required: false })
  public contactPhone?: string;

  @Prop({ required: false, default: 'email', type: String })
  public contactMethod?: ClientContactMethod;
}

const ClientSchema = SchemaFactory.createForClass(ClientRecord);

export { ClientSchema };
