import { Model, Types } from 'mongoose';
import { TimesheetEventRecord } from '../schemas/timesheet-event.schema';
import { BadRequestException } from '@nestjs/common';

export const maxTotalDurationMinutes = 24 * 60;

export const validateTotalDuration = async (
  timesheetEventModel: Model<TimesheetEventRecord>,
  userId: Types.ObjectId,
  eventDate: string,
  eventMinutes: number
): Promise<void> => {
  const durationTotalMinutes = await sumDurationMinutesForDate(
    timesheetEventModel,
    userId,
    eventDate
  );

  if (durationTotalMinutes + eventMinutes > maxTotalDurationMinutes) {
    throw new BadRequestException(
      `Total duration for all events for user '${userId}' on ${eventDate} exceeds 24 hours.`
    );
  }
};

const sumDurationMinutesForDate = async (
  timesheetEventModel: Model<TimesheetEventRecord>,
  userId: Types.ObjectId,
  eventDate: string
): Promise<number> => {
  const eventsForDate = await timesheetEventModel
    .find({
      user: userId,
      date: eventDate,
    })
    .exec();
  return eventsForDate.reduce(
    (accumulator, { durationMinutes }: TimesheetEventRecord) =>
      accumulator + durationMinutes,
    0
  );
};
