import { ApiProperty } from '@nestjs/swagger';
import { ClientStatus, clientStatusList } from '@hrms/shared/models';
import { IsIn, IsOptional } from 'class-validator';

export class GetClientsQueryParams {
  @ApiProperty({
    example: 'active',
    enum: clientStatusList,
    required: false,
  })
  @IsOptional()
  @IsIn(clientStatusList)
  public status: ClientStatus | undefined;
}
