import {
  BadRequestException,
  ConflictException,
  Injectable,
  InternalServerErrorException,
  NotFoundException,
} from '@nestjs/common';
import { InjectModel } from '@nestjs/mongoose';
import { Model, Types } from 'mongoose';
import { ActivityStatus } from '@hrms/shared/models';
import { ActivityDocument, ActivityRecord } from './schemas/activity.schema';
import { TimesheetEventRecord } from '../timesheet-events/schemas/timesheet-event.schema';
import { UserRecord } from '../users/schemas/user.schema';
import { CreateActivityDto } from './dtos/create-activity.dto';
import { UpdateActivityDto } from './dtos/update-activity.dto';
import { ProjectsService } from '../projects/projects.service';

@Injectable()
export class ActivitiesService {
  constructor(
    @InjectModel(ActivityRecord.name)
    private activityModel: Model<ActivityRecord>,
    @InjectModel(TimesheetEventRecord.name)
    private timesheetEventModel: Model<TimesheetEventRecord>,
    @InjectModel(UserRecord.name)
    private userModel: Model<UserRecord>,
    private projectsService: ProjectsService
  ) {}

  public async findById(id: string): Promise<ActivityRecord | undefined> {
    const result = await this.activityModel.findById(id).exec();

    return result ?? undefined;
  }

  public async getActivityProjectId(
    activityId: string | Types.ObjectId | undefined
  ): Promise<Types.ObjectId | undefined> {
    if (!activityId) {
      return undefined;
    }

    const activity = await this.activityModel.findById(activityId).exec();
    return activity?.project._id ?? undefined;
  }

  public async getActivities(
    status?: ActivityStatus,
    clientId?: string,
    projectId?: string
  ): Promise<ActivityRecord[]> {
    if (!status && !clientId && !projectId) {
      return await this.activityModel.find().exec();
    }

    const filters: Record<string, string> = {};

    if (status) {
      filters.status = status;
    }
    if (clientId) {
      filters.client = clientId;
    }
    if (projectId) {
      filters.project = projectId;
    }

    return await this.activityModel.find(filters).exec();
  }

  public async createActivity(
    createActivityDto: CreateActivityDto
  ): Promise<string> {
    const { name, projectId, status } = createActivityDto;

    const projectRecord = await this.projectsService.findById(projectId);
    if (!projectRecord) {
      throw new BadRequestException('Project does not exist');
    }

    const existingActivityRecord = await this.findActivityByName(
      name,
      projectId
    );
    if (existingActivityRecord) {
      throw new ConflictException(
        'Activity with same name already exists for project'
      );
    }

    const clientId = await this.projectsService.getProjectClientId(projectId);
    const activityModel = new this.activityModel({
      name: name.trim(),
      client: clientId,
      project: projectId,
      status,
    });

    const activityId = (await activityModel.save())?.id;
    if (!activityId) {
      throw new InternalServerErrorException('Failed to save activity');
    }

    return activityId;
  }

  public async updateActivity(
    activityId: string,
    updateActivityDto: UpdateActivityDto
  ): Promise<void> {
    const { name, status } = updateActivityDto;

    const targetActivity = await this.findById(activityId);
    if (!targetActivity) {
      throw new NotFoundException(`Activity id not found`);
    }

    if (name) {
      const projectId = targetActivity.project._id.toString();
      const duplicateActivity = await this.findActivityByName(name, projectId);

      if (
        duplicateActivity &&
        duplicateActivity._id.toString() !== activityId
      ) {
        throw new ConflictException(
          'Activity with same name already exists for project'
        );
      }
    }

    await (<ActivityDocument>targetActivity).updateOne({
      name: name?.trim(),
      status,
    });
  }

  public async deleteActivity(activityId: string): Promise<void> {
    const targetActivity = await this.activityModel.findById(activityId);

    if (!targetActivity) {
      throw new NotFoundException('Activity id not found');
    }

    const assignedUser = await this.userModel.findOne({
      activities: activityId,
    });

    if (assignedUser) {
      throw new BadRequestException(
        'Cannot delete activity assigned to user(s)'
      );
    }

    const timesheetEvent = await this.timesheetEventModel.findOne({
      activity: activityId,
    });

    if (timesheetEvent) {
      throw new BadRequestException(
        'Cannot delete activity referenced in timesheet event(s)'
      );
    }

    await targetActivity.deleteOne();
  }

  private findActivityByName(
    name: string,
    projectId: string
  ): Promise<ActivityRecord | null> {
    const activityNameRegex = '^' + name.trim() + '$';

    return this.activityModel.findOne({
      name: { $regex: activityNameRegex, $options: 'i' },
      project: projectId,
    });
  }
}
