import { TimesheetEventType } from '@hrms/shared/models';
import { UserRecord } from '../../users/schemas/user.schema';
import { BadRequestException } from '@nestjs/common';
import { UserRecordHelper } from '../../users/helpers/user-record.helper';
import { ActivityRecord } from '../../activities/schemas/activity.schema';

export const validateActivityId = (
  userRecord: UserRecord,
  userRecordHelper: UserRecordHelper,
  eventType: TimesheetEventType,
  activityId: string | undefined
): ActivityRecord | undefined => {
  if (eventType !== 'work') {
    return undefined;
  }

  const assignedActivity = activityId
    ? userRecordHelper.getAssignedActivity(userRecord, activityId)
    : undefined;

  if (!assignedActivity) {
    throw new BadRequestException(
      `activityId '${activityId}' not available for user '${userRecord._id}'`
    );
  }

  return assignedActivity;
};
