import {
  BadRequestException,
  ConflictException,
  Injectable,
  InternalServerErrorException,
  NotFoundException,
} from '@nestjs/common';
import { InjectModel } from '@nestjs/mongoose';
import { Model, Types } from 'mongoose';

import { ProjectStatus } from '@hrms/shared/models';
import { ProjectDocument, ProjectRecord } from './schemas/project.schema';
import { CreateProjectDto } from './dtos/create-project.dto';
import { UpdateProjectDto } from './dtos/update-project.dto';
import { ClientsService } from '../clients/clients.service';
import { ActivityRecord } from '../activities/schemas/activity.schema';
import { TimesheetEventRecord } from '../timesheet-events/schemas/timesheet-event.schema';
import { UserRecord } from '../users/schemas/user.schema';

@Injectable()
export class ProjectsService {
  constructor(
    @InjectModel(ProjectRecord.name)
    private projectModel: Model<ProjectRecord>,
    @InjectModel(ActivityRecord.name)
    private activityModel: Model<ActivityRecord>,
    @InjectModel(TimesheetEventRecord.name)
    private timesheetEventModel: Model<TimesheetEventRecord>,
    @InjectModel(UserRecord.name)
    private userModel: Model<UserRecord>,
    private clientsService: ClientsService
  ) {}

  public async findById(id: string): Promise<ProjectRecord | undefined> {
    const result = await this.projectModel.findById(id).exec();

    return result ?? undefined;
  }

  public async getProjectClientId(
    projectId: string | Types.ObjectId | undefined
  ): Promise<Types.ObjectId | undefined> {
    if (!projectId) {
      return undefined;
    }

    const project = await this.projectModel.findById(projectId).exec();
    return project?.client._id ?? undefined;
  }

  public async getProjects(
    status?: ProjectStatus,
    clientId?: string
  ): Promise<ProjectRecord[]> {
    if (!status && !clientId) {
      return await this.projectModel.find().exec();
    }

    const filters: Record<string, string> = {};

    if (status) {
      filters.status = status;
    }
    if (clientId) {
      filters.client = clientId;
    }

    return await this.projectModel.find(filters).exec();
  }

  public async createProject(
    createProjectDto: CreateProjectDto
  ): Promise<string> {
    const { name, clientId, status } = createProjectDto;

    const clientRecord = await this.clientsService.findById(clientId);
    if (!clientRecord) {
      throw new BadRequestException('Client does not exist');
    }

    const existingProjectRecord = await this.findProjectByName(name, clientId);
    if (existingProjectRecord) {
      throw new ConflictException(
        'Project with same name already exists for client'
      );
    }

    const projectModel = new this.projectModel({
      name: name.trim(),
      client: clientId,
      status,
    });

    const projectId = (await projectModel.save())?.id;
    if (!projectId) {
      throw new InternalServerErrorException('Failed to save project');
    }

    return projectId;
  }

  public async updateProject(
    projectId: string,
    updateProjectDto: UpdateProjectDto
  ): Promise<void> {
    const { name, status } = updateProjectDto;

    const targetProject = await this.findById(projectId);
    if (!targetProject) {
      throw new NotFoundException(`Project id not found`);
    }

    if (name) {
      const clientId = targetProject.client._id.toString();
      const duplicateProject = await this.findProjectByName(name, clientId);

      if (duplicateProject && duplicateProject._id.toString() !== projectId) {
        throw new ConflictException(
          'Project with same name already exists for client'
        );
      }
    }

    await (<ProjectDocument>targetProject).updateOne({
      name: name?.trim(),
      status,
    });
  }

  public async deleteProject(projectId: string): Promise<void> {
    const targetProject = await this.projectModel.findById(projectId);

    if (!targetProject) {
      throw new NotFoundException('Project id not found');
    }

    const assignedUser = await this.userModel.findOne({
      projects: projectId,
    });

    if (assignedUser) {
      throw new BadRequestException(
        'Cannot delete project assigned to user(s)'
      );
    }

    const childActivities = await this.activityModel.find({
      project: projectId,
    });

    for (const activity of childActivities) {
      const activityId = activity._id.toString();

      const timesheetEvent = await this.timesheetEventModel.findOne({
        activity: activityId,
      });

      if (timesheetEvent) {
        throw new BadRequestException(
          'Cannot delete project with any activity referenced in timesheet event(s)'
        );
      }
    }

    await this.activityModel.deleteMany({
      project: projectId,
    });
    await targetProject.deleteOne();
  }

  private findProjectByName(
    name: string,
    clientId: string
  ): Promise<ProjectRecord | null> {
    const projectNameRegex = '^' + name.trim() + '$';

    return this.projectModel.findOne({
      name: { $regex: projectNameRegex, $options: 'i' },
      client: clientId,
    });
  }
}
