import { Model, Types } from 'mongoose';
import {
  TimesheetEventRecord,
  TimesheetEventDocument,
} from '../schemas/timesheet-event.schema';
import { BadRequestException, NotFoundException } from '@nestjs/common';

export const validateEventId = async (
  timesheetEventModel: Model<TimesheetEventRecord>,
  userId: Types.ObjectId,
  eventId: string
): Promise<TimesheetEventDocument> => {
  const event = await timesheetEventModel.findById(eventId).exec();
  if (!event) {
    throw new NotFoundException(
      `Could not find timesheet event for '${eventId}'`
    );
  }

  if (!event.user._id.equals(userId)) {
    throw new BadRequestException(
      `Timesheet event '${eventId}' does not belong to user '${userId}'`
    );
  }

  return event;
};
