import { Injectable, InternalServerErrorException } from '@nestjs/common';
import { UserRecord } from '../users/schemas/user.schema';
import { WorkSummaryRecord } from './schemas/work-summary.schema';
import { InjectModel } from '@nestjs/mongoose';
import { Model } from 'mongoose';
import { DateUtils } from '../../validators/date-utils.validator';
import { CreateWorkSummaryDto } from './dtos/create-work-summary.dto';
import { UserRecordHelper } from '../users/helpers/user-record.helper';
import { validateProjectId } from '../timesheet-events/helpers/project-validation.helper';
import { UpdateWorkSummaryDto } from './dtos/update-work-summary.dto';
import { validateWorkSummaryId } from './helpers/summary-validation.helper';
import { ProjectsService } from '../projects/projects.service';

@Injectable()
export class WorkSummariesService {
  constructor(
    @InjectModel(WorkSummaryRecord.name)
    private workSummaryModel: Model<WorkSummaryRecord>,
    private projectsService: ProjectsService,
    private userRecordHelper: UserRecordHelper
  ) {}

  public async getWorkSummaries(
    userRecord: UserRecord,
    startDate: string,
    endDate: string,
    projectId?: string
  ): Promise<WorkSummaryRecord[]> {
    DateUtils.validateDateRange(startDate, endDate);

    const query: Record<string, unknown> = {
      user: userRecord._id,
      date: {
        $gte: startDate,
        $lte: endDate,
      },
    };

    if (projectId) {
      query.project = projectId;
    }

    return await this.workSummaryModel.find(query).exec();
  }

  public async createWorkSummary(
    userRecord: UserRecord,
    createSummaryDto: CreateWorkSummaryDto
  ): Promise<string> {
    const userId = userRecord._id;

    const { date, projectId, summary, isSREDEligible } = createSummaryDto;

    const assignedProject = validateProjectId(
      userRecord,
      this.userRecordHelper,
      projectId
    );
    const clientId = await this.projectsService.getProjectClientId(projectId);

    const workSummary = new this.workSummaryModel({
      user: userId,
      date,
      client: clientId,
      project: assignedProject?._id,
      summary,
      isSREDEligible,
    });

    const workSummaryId = (await workSummary.save())?.id;
    if (!workSummaryId) {
      throw new InternalServerErrorException('Failed to save new work summary');
    }

    return workSummaryId;
  }

  public async updateWorkSummary(
    workSummaryId: string,
    userRecord: UserRecord,
    updateWorkSummaryDto: UpdateWorkSummaryDto
  ): Promise<void> {
    const userId = userRecord._id;
    const { summary, isSREDEligible } = updateWorkSummaryDto;

    const existingSummary = await validateWorkSummaryId(
      this.workSummaryModel,
      userId,
      workSummaryId
    );

    await existingSummary.updateOne({
      summary,
      isSREDEligible,
    });
  }

  public async deleteWorkSummary(
    workSummaryId: string,
    userRecord: UserRecord
  ): Promise<void> {
    const userId = userRecord._id;

    const existingSummary = await validateWorkSummaryId(
      this.workSummaryModel,
      userId,
      workSummaryId
    );

    await existingSummary.deleteOne();
  }
}
