import { Module } from '@nestjs/common';
import { MongooseModule } from '@nestjs/mongoose';
import { ClientsController } from './clients.controller';
import { ClientsService } from './clients.service';
import { ClientRecord, ClientSchema } from './schemas/client.schema';
import {
  ActivityRecord,
  ActivitySchema,
} from '../activities/schemas/activity.schema';
import {
  ProjectRecord,
  ProjectSchema,
} from '../projects/schemas/project.schema';
import {
  TimesheetEventRecord,
  TimesheetEventSchema,
} from '../timesheet-events/schemas/timesheet-event.schema';
import { UserRecord, UserSchema } from '../users/schemas/user.schema';
import { AuthModule } from '../auth/auth.module';

@Module({
  controllers: [ClientsController],
  providers: [ClientsService],
  imports: [
    MongooseModule.forFeature([
      { name: ClientRecord.name, schema: ClientSchema },
    ]),
    MongooseModule.forFeature([
      {
        name: ActivityRecord.name,
        schema: ActivitySchema,
      },
    ]),
    MongooseModule.forFeature([
      {
        name: ProjectRecord.name,
        schema: ProjectSchema,
      },
    ]),
    MongooseModule.forFeature([
      {
        name: TimesheetEventRecord.name,
        schema: TimesheetEventSchema,
      },
    ]),
    MongooseModule.forFeature([
      {
        name: UserRecord.name,
        schema: UserSchema,
      },
    ]),
    AuthModule,
  ],
  exports: [ClientsService],
})
export class ClientsModule {}
