import { ApiProperty } from '@nestjs/swagger';
import { IsMongoId } from 'class-validator';
import { ICreateWorkSummaryDto } from '@hrms/shared/dtos';
import { IsDateOnlyString } from '../../../validators/is-date-only-string.validator';
import { IsNotBlankString } from '../../../validators/is-not-blank-string.validator';

export class CreateWorkSummaryDto implements ICreateWorkSummaryDto {
  @ApiProperty({ example: new Date().toISOString().split('T')[0] })
  @IsDateOnlyString()
  public readonly date: string;

  @ApiProperty()
  @IsMongoId()
  public readonly projectId: string;

  @ApiProperty()
  @IsNotBlankString()
  public readonly summary: string;

  @ApiProperty()
  public readonly isSREDEligible?: boolean;
}
