import { Injectable, InternalServerErrorException } from '@nestjs/common';
import { InjectModel } from '@nestjs/mongoose';
import { Model } from 'mongoose';
import { TimesheetEventRecord } from './schemas/timesheet-event.schema';
import { CreateTimesheetEventDto } from './dtos/create-timesheet-event.dto';
import { UserRecord } from '../users/schemas/user.schema';
import { UserRecordHelper } from '../users/helpers/user-record.helper';
import { UpdateTimesheetEventDto } from './dtos/update-timesheet-event.dto';
import { validateTotalDuration } from './helpers/duration-validation.helper';
import { validateActivityId } from './helpers/activity-validation.helper';
import { validateEventId } from './helpers/event-validation.helper';
import { ActivitiesService } from '../activities/activities.service';
import { ProjectsService } from '../projects/projects.service';
import { DateUtils } from '../../validators/date-utils.validator';

@Injectable()
export class TimesheetEventsService {
  constructor(
    @InjectModel(TimesheetEventRecord.name)
    private timesheetEventModel: Model<TimesheetEventRecord>,
    private userRecordHelper: UserRecordHelper,
    private activitiesService: ActivitiesService,
    private projectsService: ProjectsService
  ) {}

  public async getTimesheetEvents(
    userRecord: UserRecord,
    startDate: string,
    endDate: string
  ): Promise<TimesheetEventRecord[]> {
    DateUtils.validateDateRange(startDate, endDate);

    return await this.timesheetEventModel
      .find({
        user: userRecord._id,
        date: {
          $gte: startDate,
          $lte: endDate,
        },
      })
      .exec();
  }

  public async createTimesheetEvent(
    userRecord: UserRecord,
    createEventDto: CreateTimesheetEventDto
  ): Promise<string> {
    const userId = userRecord._id;

    const {
      eventType,
      date: eventDate,
      durationMinutes: eventMinutes,
      comment,
    } = createEventDto;

    const assignedActivity = validateActivityId(
      userRecord,
      this.userRecordHelper,
      eventType,
      createEventDto.activityId
    );

    await validateTotalDuration(
      this.timesheetEventModel,
      userId,
      eventDate,
      eventMinutes
    );

    const projectId = await this.activitiesService.getActivityProjectId(
      assignedActivity?._id
    );
    const clientId = await this.projectsService.getProjectClientId(projectId);

    const event = new this.timesheetEventModel({
      user: userId,
      date: eventDate,
      durationMinutes: eventMinutes,
      eventType,
      comment,
      activity: assignedActivity?._id,
      project: projectId,
      client: clientId,
    });

    const eventId = (await event.save())?.id;
    if (!eventId) {
      throw new InternalServerErrorException(
        'Failed to save new timesheet event'
      );
    }

    return eventId;
  }

  public async updateTimesheetEvent(
    eventId: string,
    userRecord: UserRecord,
    updateEventDto: UpdateTimesheetEventDto
  ): Promise<void> {
    const userId = userRecord._id;

    const existingEvent = await validateEventId(
      this.timesheetEventModel,
      userId,
      eventId
    );

    const {
      eventType,
      date: updateEventDate,
      durationMinutes: updateEventMinutes,
      comment,
    } = updateEventDto;

    const assignedActivity = validateActivityId(
      userRecord,
      this.userRecordHelper,
      eventType,
      updateEventDto.activityId
    );

    const eventMinutesDelta =
      existingEvent.date === updateEventDate
        ? updateEventMinutes - existingEvent.durationMinutes
        : updateEventMinutes;
    await validateTotalDuration(
      this.timesheetEventModel,
      userId,
      updateEventDate,
      eventMinutesDelta
    );

    const projectId = await this.activitiesService.getActivityProjectId(
      assignedActivity?._id
    );
    const clientId = await this.projectsService.getProjectClientId(projectId);

    await existingEvent.updateOne({
      date: updateEventDate,
      durationMinutes: updateEventMinutes,
      eventType,
      comment,
      activity: assignedActivity?._id ?? null,
      project: projectId ?? null,
      client: clientId ?? null,
    });
  }

  public async deleteTimesheetEvent(
    eventId: string,
    userRecord: UserRecord
  ): Promise<void> {
    const userId = userRecord._id;

    const existingEvent = await validateEventId(
      this.timesheetEventModel,
      userId,
      eventId
    );

    await existingEvent.deleteOne();
  }
}
