import { ApiProperty } from '@nestjs/swagger';
import { ActivityStatus, activityStatusList } from '@hrms/shared/models';
import { IsIn, IsMongoId, IsOptional } from 'class-validator';

export class GetActivitiesQueryParams {
  @ApiProperty({
    example: 'enabled',
    enum: activityStatusList,
    required: false,
  })
  @IsOptional()
  @IsIn(activityStatusList)
  public status: ActivityStatus | undefined;

  @ApiProperty({
    type: String,
    required: false,
  })
  @IsOptional()
  @IsMongoId()
  public clientId: string | undefined;

  @ApiProperty({
    type: String,
    required: false,
  })
  @IsOptional()
  @IsMongoId()
  public projectId: string | undefined;
}
