import { Schema, Prop, SchemaFactory } from '@nestjs/mongoose';
import { HydratedDocument, SchemaTypes, Types } from 'mongoose';
import { ClientRecord } from '../../clients/schemas/client.schema';
import { ProjectRecord } from '../../projects/schemas/project.schema';
import { ActivityStatus } from '@hrms/shared/models';

export type ActivityDocument = HydratedDocument<ActivityRecord>;

@Schema({ collection: 'activities' })
export class ActivityRecord {
  public _id: Types.ObjectId;

  @Prop({ type: SchemaTypes.ObjectId, ref: ClientRecord.name, required: true })
  public client: ClientRecord;

  @Prop({ type: SchemaTypes.ObjectId, ref: ProjectRecord.name, required: true })
  public project: ProjectRecord;

  @Prop({ required: true })
  public name: string;

  @Prop({ required: true, default: 'enabled', type: String })
  public status: ActivityStatus;
}

const ActivitySchema = SchemaFactory.createForClass(ActivityRecord);

export { ActivitySchema };
