import {
  BadRequestException,
  ConflictException,
  Injectable,
  InternalServerErrorException,
} from '@nestjs/common';
import { InjectModel } from '@nestjs/mongoose';
import { Model } from 'mongoose';
import { UserRecord } from './schemas/user.schema';
import * as bcrypt from 'bcrypt';
import { assertUserExists } from '../../assertions/assert-user-exists';
import { CreateUserDto } from './dtos/create-user.dto';
import { UserStatus } from '@hrms/shared/models';
import { ProjectsService } from '../projects/projects.service';
import { ActivitiesService } from '../activities/activities.service';
import { UserNotFoundException } from '../../exceptions/user-not-found.exception';
import { ProjectRecord } from '../projects/schemas/project.schema';
import { ClientRecord } from '../clients/schemas/client.schema';

@Injectable()
export class UsersService {
  constructor(
    @InjectModel(UserRecord.name) private userModel: Model<UserRecord>,
    private projectsService: ProjectsService,
    private activitiesService: ActivitiesService
  ) {}

  public async findAll(): Promise<UserRecord[]> {
    const findAll = this.userModel.find();
    return (await this.populateAndExecuteQuery(findAll)) ?? [];
  }

  public async findById(id: string): Promise<UserRecord | undefined> {
    const findById = this.userModel.findById(id);
    return (await this.populateAndExecuteQuery(findById)) ?? undefined;
  }

  public async findByEmail(email: string): Promise<UserRecord | undefined> {
    const findByEmail = this.findEnabledUserByEmail(email);
    return await this.populateAndExecuteQuery(findByEmail);
  }

  public async createUser(createUserDto: CreateUserDto): Promise<string> {
    const { firstName, lastName, email, password, role, status } =
      createUserDto;

    const existingUserRecord = await this.findEnabledUserByEmail(email);
    if (existingUserRecord) {
      throw new ConflictException('User already exists');
    }

    const passwordHash = await bcrypt.hash(password, 10);

    const userModel = new this.userModel({
      firstName,
      lastName,
      email,
      passwordHash,
      role,
      status,
      clients: [],
      projects: [],
      activities: [],
    });

    const userId = (await userModel.save())?.id;
    if (!userId) {
      throw new InternalServerErrorException('Failed to save user');
    }

    return userId;
  }

  public async updateUserStatus(
    userId: string,
    status: UserStatus
  ): Promise<void> {
    const userRecord = await this.userModel.findById(userId);
    if (!userRecord) {
      throw new UserNotFoundException(userId);
    }

    await userRecord.updateOne({
      status,
    });
  }

  public async updateUserPassword(
    userId: string,
    password: string
  ): Promise<void> {
    const userRecord = await this.userModel.findById(userId);
    if (!userRecord) {
      throw new UserNotFoundException(userId);
    }

    const passwordHash = await bcrypt.hash(password, 10);

    await userRecord.updateOne({
      passwordHash,
    });
  }

  public async updateUserActivities(
    userId: string,
    projectId: string,
    activityIds: string[] = []
  ): Promise<void> {
    const userRecord = await this.findById(userId);
    if (!userRecord) {
      throw new UserNotFoundException(userId);
    }

    const projectRecord = await this.projectsService.findById(projectId);
    if (!projectRecord) {
      throw new BadRequestException(`project with id '${projectId}' not found`);
    }

    const uniqueActivityIds = [...new Set(activityIds)];
    await this.validateActivitiesBelongToProject(projectId, uniqueActivityIds);

    if (!uniqueActivityIds.length) {
      await this.removeAllUserActivitiesForProject(userRecord, projectRecord);
    } else {
      await this.updateUserActivitiesForProject(
        userRecord,
        projectRecord,
        uniqueActivityIds
      );
    }
  }

  public async changeUserPassword(
    userId: string,
    currentPassword: string,
    newPassword: string
  ): Promise<void> {
    const userRecord = await this.userModel.findById(userId);
    assertUserExists(userRecord, userId);

    const isCurrentPasswordCorrect = await bcrypt.compare(
      currentPassword,
      userRecord.passwordHash
    );

    if (!isCurrentPasswordCorrect) {
      throw new BadRequestException('Invalid current password');
    }

    const hashedNewPassword = await bcrypt.hash(newPassword, 10);
    userRecord.passwordHash = hashedNewPassword;
    await userRecord.save();
  }

  private async populateAndExecuteQuery<T>(find): Promise<T | undefined> {
    return (
      (await find
        .populate('clients')
        .populate('projects')
        .populate('activities')
        .exec()) ?? undefined
    );
  }

  private findEnabledUserByEmail(email: string): Promise<UserRecord | null> {
    return this.userModel.findOne({
      email: email.toLowerCase(),
      status: 'enabled',
    });
  }

  private async validateActivitiesBelongToProject(
    projectId: string,
    activityIds: string[]
  ): Promise<void> {
    const projectActivities = await this.activitiesService.getActivities(
      undefined,
      undefined,
      projectId
    );
    const projectActivityIds = projectActivities.map((activity) =>
      activity._id.toString()
    );

    const invalidActivityIds = activityIds.filter(
      (activityId) => !projectActivityIds.includes(activityId)
    );

    if (invalidActivityIds.length > 0) {
      throw new BadRequestException(
        `one or more activities do not belong to project ${projectId}: ${invalidActivityIds.join(
          ', '
        )}`
      );
    }
  }

  private async removeAllUserActivitiesForProject(
    userRecord: UserRecord,
    projectRecord: ProjectRecord
  ): Promise<void> {
    const projectId = projectRecord._id;

    const remainingActivities = userRecord.activities.filter(
      (activity) => !activity.project._id.equals(projectId)
    );

    const remainingProjects = userRecord.projects.filter(
      (project) => !project._id.equals(projectId)
    );

    const remainingClients: ClientRecord[] = [];
    remainingProjects.forEach((project) => {
      const isClientInRemainingClients = remainingClients.find((clientRecord) =>
        clientRecord._id.equals(project.client._id)
      );
      if (!isClientInRemainingClients) {
        remainingClients.push(project.client);
      }
    });

    await this.userModel.updateOne(
      { _id: userRecord._id },
      {
        clients: remainingClients,
        projects: remainingProjects,
        activities: remainingActivities,
      }
    );
  }

  private async updateUserActivitiesForProject(
    userRecord: UserRecord,
    projectRecord: ProjectRecord,
    uniqueActivityIds: string[]
  ): Promise<void> {
    const updatedClients = [...userRecord.clients];

    const clientRecord = projectRecord.client;

    const foundClient = userRecord.clients.find((client) =>
      client._id.equals(clientRecord._id)
    );
    if (!foundClient) {
      updatedClients.push(clientRecord);
    }

    const updatedProjects = [...userRecord.projects];
    const foundProject = updatedProjects.find((project) =>
      project._id.equals(projectRecord._id)
    );
    if (!foundProject) {
      updatedProjects.push(projectRecord);
    }

    const updatedActivitiesIds = userRecord.activities
      .filter((activity) => !activity.project._id.equals(projectRecord._id))
      .map((activity) => activity._id.toString());

    const updatedActivityIdSet = new Set([...updatedActivitiesIds]);
    uniqueActivityIds.forEach((activityId) => {
      updatedActivityIdSet.add(activityId);
    });

    await this.userModel.updateOne(
      { _id: userRecord._id },
      {
        clients: updatedClients,
        projects: updatedProjects,
        activities: [...updatedActivityIdSet],
      }
    );
  }
}
