import {
  Body,
  Controller,
  Delete,
  Get,
  HttpCode,
  HttpStatus,
  Param,
  Patch,
  Post,
  Put,
  Query,
  Req,
} from '@nestjs/common';
import {
  ApiBadRequestResponse,
  ApiBearerAuth,
  ApiCreatedResponse,
  ApiInternalServerErrorResponse,
  ApiNoContentResponse,
  ApiNotFoundResponse,
  ApiOkResponse,
  ApiParam,
  ApiTags,
  ApiUnauthorizedResponse,
} from '@nestjs/swagger';
import { User } from '@hrms/shared/models';
import { Request } from 'express';
import { getCurrentUserId } from '../../utils/api.helper';
import { TimesheetEventsService } from '../timesheet-events/timesheet-events.service';
import { CreateTimesheetEventDto } from '../timesheet-events/dtos/create-timesheet-event.dto';
import { UsersAssembler } from '../users/users.assembler';
import { UsersService } from '../users/users.service';
import { assertUserExists } from '../../assertions/assert-user-exists';
import { UpdateTimesheetEventDto } from '../timesheet-events/dtos/update-timesheet-event.dto';
import { UpdateTimesheetEventParams } from '../timesheet-events/params/update-timesheet-event.params';
import { TimesheetEvent } from '../timesheet-events/models/timesheet-event';
import { GetTimesheetEventsQueryParams } from '../timesheet-events/query-params/get-timesheet-events.query-params';
import { TimesheetEventsAssembler } from '../timesheet-events/timesheet-events.assembler';
import { DeleteTimesheetEventParams } from '../timesheet-events/params/delete-timesheet-event.params';
import { WorkSummary } from '../work-summaries/models/work-summary';
import { WorkSummariesService } from '../work-summaries/work-summaries.service';
import { WorkSummariesAssembler } from '../work-summaries/work-summaries.assembler';
import { GetWorkSummariesQueryParams } from '../work-summaries/query-params/get-work-summaries.query-params';
import { CreateWorkSummaryDto } from '../work-summaries/dtos/create-work-summary.dto';
import { UpdateWorkSummaryDto } from '../work-summaries/dtos/update-work-summary.dto';
import { UpdateWorkSummaryParams } from '../work-summaries/params/update-work-summary.params';
import { DeleteWorkSummaryParams } from '../work-summaries/params/delete-work-summary.params';
import { ICreatedIdDto } from '@hrms/shared/dtos';
import { ChangePasswordDto } from './dtos/change-password.dto';

@ApiTags('Current user')
@Controller('user')
export class UserController {
  constructor(
    private usersService: UsersService,
    private usersAssembler: UsersAssembler,
    private timesheetEventService: TimesheetEventsService,
    private timesheetEventsAssembler: TimesheetEventsAssembler,
    private workSummaryService: WorkSummariesService,
    private workSummariesAssembler: WorkSummariesAssembler
  ) {}

  @ApiBearerAuth()
  @ApiOkResponse({ description: 'Success', type: User })
  @ApiUnauthorizedResponse({ description: 'Unauthorized' })
  @ApiInternalServerErrorResponse({ description: 'Internal error' })
  @Get()
  public async getUserInformation(@Req() request: Request): Promise<User> {
    const userId = getCurrentUserId(request);

    const userRecord = await this.usersService.findById(userId);
    assertUserExists(userRecord, userId);

    return this.usersAssembler.assembleUser(userRecord);
  }

  @ApiBearerAuth()
  @ApiOkResponse({
    description: 'Success',
    type: TimesheetEvent,
    isArray: true,
  })
  @ApiBadRequestResponse({ description: 'Bad request' })
  @ApiUnauthorizedResponse({ description: 'Unauthorized' })
  @ApiInternalServerErrorResponse({ description: 'Internal error' })
  @Get('timesheet-events')
  public async getTimesheetEvents(
    @Req() request: Request,
    @Query() { startDate, endDate }: GetTimesheetEventsQueryParams
  ): Promise<TimesheetEvent[]> {
    const userId = getCurrentUserId(request);

    const userRecord = await this.usersService.findById(userId);
    assertUserExists(userRecord, userId);

    const timesheetEventRecords =
      await this.timesheetEventService.getTimesheetEvents(
        userRecord,
        startDate,
        endDate
      );

    return this.timesheetEventsAssembler.assembleEvents(timesheetEventRecords);
  }

  @ApiBearerAuth()
  @ApiCreatedResponse({ description: 'Created', type: String })
  @ApiBadRequestResponse({ description: 'Bad request' })
  @ApiUnauthorizedResponse({ description: 'Unauthorized' })
  @ApiInternalServerErrorResponse({ description: 'Internal error' })
  @Post('timesheet-events')
  public async createTimesheetEvent(
    @Req() request: Request,
    @Body() createEventDto: CreateTimesheetEventDto
  ): Promise<ICreatedIdDto> {
    const userId = getCurrentUserId(request);

    const userRecord = await this.usersService.findById(userId);
    assertUserExists(userRecord, userId);

    const id = await this.timesheetEventService.createTimesheetEvent(
      userRecord,
      createEventDto
    );

    return { id };
  }

  @ApiBearerAuth()
  @ApiNoContentResponse({ description: 'No content' })
  @ApiBadRequestResponse({ description: 'Bad request' })
  @ApiNotFoundResponse({ description: 'Not found' })
  @ApiUnauthorizedResponse({ description: 'Unauthorized' })
  @ApiInternalServerErrorResponse({ description: 'Internal error' })
  @ApiParam({ required: true, name: 'eventId' })
  @HttpCode(HttpStatus.NO_CONTENT)
  @Put('timesheet-events/:eventId')
  public async updateTimesheetEvent(
    @Req() request: Request,
    @Param() { eventId }: UpdateTimesheetEventParams,
    @Body() updateEventDto: UpdateTimesheetEventDto
  ): Promise<void> {
    const userId = getCurrentUserId(request);

    const userRecord = await this.usersService.findById(userId);
    assertUserExists(userRecord, userId);

    await this.timesheetEventService.updateTimesheetEvent(
      eventId,
      userRecord,
      updateEventDto
    );
  }

  @ApiBearerAuth()
  @ApiNoContentResponse({ description: 'No content' })
  @ApiBadRequestResponse({ description: 'Bad request' })
  @ApiNotFoundResponse({ description: 'Not found' })
  @ApiUnauthorizedResponse({ description: 'Unauthorized' })
  @ApiInternalServerErrorResponse({ description: 'Internal error' })
  @ApiParam({ required: true, name: 'eventId' })
  @HttpCode(HttpStatus.NO_CONTENT)
  @Delete('timesheet-events/:eventId')
  public async deleteTimesheetEvent(
    @Req() request: Request,
    @Param() { eventId }: DeleteTimesheetEventParams
  ): Promise<void> {
    const userId = getCurrentUserId(request);

    const userRecord = await this.usersService.findById(userId);
    assertUserExists(userRecord, userId);

    await this.timesheetEventService.deleteTimesheetEvent(eventId, userRecord);
  }

  @ApiBearerAuth()
  @ApiOkResponse({
    description: 'Success',
    type: WorkSummary,
    isArray: true,
  })
  @ApiBadRequestResponse({ description: 'Bad request' })
  @ApiUnauthorizedResponse({ description: 'Unauthorized' })
  @ApiInternalServerErrorResponse({ description: 'Internal error' })
  @Get('work-summaries')
  public async getWorkSummaries(
    @Req() request: Request,
    @Query() { startDate, endDate, projectId }: GetWorkSummariesQueryParams
  ): Promise<WorkSummary[]> {
    const userId = getCurrentUserId(request);

    const userRecord = await this.usersService.findById(userId);
    assertUserExists(userRecord, userId);

    const workSummaryRecords = await this.workSummaryService.getWorkSummaries(
      userRecord,
      startDate,
      endDate,
      projectId
    );
    return this.workSummariesAssembler.assembleWorkSummaries(
      workSummaryRecords
    );
  }

  @ApiBearerAuth()
  @ApiCreatedResponse({ description: 'Created', type: String })
  @ApiBadRequestResponse({ description: 'Bad request' })
  @ApiUnauthorizedResponse({ description: 'Unauthorized' })
  @ApiInternalServerErrorResponse({ description: 'Internal error' })
  @Post('work-summaries')
  public async createWorkSummary(
    @Req() request: Request,
    @Body() createSummaryDto: CreateWorkSummaryDto
  ): Promise<ICreatedIdDto> {
    const userId = getCurrentUserId(request);

    const userRecord = await this.usersService.findById(userId);
    assertUserExists(userRecord, userId);

    const id = await this.workSummaryService.createWorkSummary(
      userRecord,
      createSummaryDto
    );

    return { id };
  }

  @ApiBearerAuth()
  @ApiNoContentResponse({ description: 'No content' })
  @ApiBadRequestResponse({ description: 'Bad request' })
  @ApiNotFoundResponse({ description: 'Not found' })
  @ApiUnauthorizedResponse({ description: 'Unauthorized' })
  @ApiInternalServerErrorResponse({ description: 'Internal error' })
  @ApiParam({ required: true, name: 'workSummaryId' })
  @HttpCode(HttpStatus.NO_CONTENT)
  @Patch('work-summaries/:workSummaryId')
  public async updateWorkSummary(
    @Req() request: Request,
    @Param() { workSummaryId }: UpdateWorkSummaryParams,
    @Body() updateWorkSummaryDto: UpdateWorkSummaryDto
  ): Promise<void> {
    const userId = getCurrentUserId(request);

    const userRecord = await this.usersService.findById(userId);
    assertUserExists(userRecord, userId);

    await this.workSummaryService.updateWorkSummary(
      workSummaryId,
      userRecord,
      updateWorkSummaryDto
    );
  }

  @ApiBearerAuth()
  @ApiNoContentResponse({ description: 'No content' })
  @ApiBadRequestResponse({ description: 'Bad request' })
  @ApiNotFoundResponse({ description: 'Not found' })
  @ApiUnauthorizedResponse({ description: 'Unauthorized' })
  @ApiInternalServerErrorResponse({ description: 'Internal error' })
  @ApiParam({ required: true, name: 'workSummaryId' })
  @HttpCode(HttpStatus.NO_CONTENT)
  @Delete('work-summaries/:workSummaryId')
  public async deleteWorkSummary(
    @Req() request: Request,
    @Param() { workSummaryId }: DeleteWorkSummaryParams
  ): Promise<void> {
    const userId = getCurrentUserId(request);

    const userRecord = await this.usersService.findById(userId);
    assertUserExists(userRecord, userId);

    await this.workSummaryService.deleteWorkSummary(workSummaryId, userRecord);
  }

  @ApiBearerAuth()
  @ApiNoContentResponse({ description: 'No content' })
  @ApiUnauthorizedResponse({ description: 'Unauthorized' })
  @ApiInternalServerErrorResponse({ description: 'Internal error' })
  @Put('password')
  public async changePassword(
    @Req() request: Request,
    @Body() { currentPassword, newPassword }: ChangePasswordDto
  ): Promise<void> {
    const userId = getCurrentUserId(request);

    await this.usersService.changeUserPassword(
      userId,
      currentPassword,
      newPassword
    );
  }
}
