import {
  Body,
  Controller,
  Delete,
  Get,
  HttpCode,
  HttpStatus,
  Param,
  Patch,
  Post,
  Query,
  Req,
} from '@nestjs/common';
import {
  ApiBadRequestResponse,
  ApiBearerAuth,
  ApiConflictResponse,
  ApiCreatedResponse,
  ApiForbiddenResponse,
  ApiInternalServerErrorResponse,
  ApiNoContentResponse,
  ApiNotFoundResponse,
  ApiOkResponse,
  ApiParam,
  ApiTags,
  ApiUnauthorizedResponse,
} from '@nestjs/swagger';
import { ActivitiesService } from './activities.service';
import { Activity } from './models/activity';
import { GetActivitiesQueryParams } from './query-params/get-activities.query-params';
import { CreateActivityDto } from './dtos/create-activity.dto';
import { ICreatedIdDto } from '@hrms/shared/dtos';
import { UpdateActivityParams } from './params/update-activity.params';
import { UpdateActivityDto } from './dtos/update-activity.dto';
import { DeleteActivityParams } from './params/delete-activity.params';
import { AuthService } from '../auth/auth.service';

@ApiTags('Activities')
@Controller('activities')
export class ActivitiesController {
  constructor(
    private activitiesService: ActivitiesService,
    private authService: AuthService
  ) {}

  @ApiBearerAuth()
  @ApiOkResponse({
    description: 'Success',
    type: Activity,
    isArray: true,
  })
  @ApiBadRequestResponse({
    description:
      'Invalid status \t\n Invalid client id format (not a MongoDb ObjectId) \t\n Invalid project id format (not a MongoDB ObjectId)',
  })
  @ApiUnauthorizedResponse({ description: 'Unauthorized' })
  @ApiInternalServerErrorResponse({ description: 'Internal error' })
  @Get()
  public async getActivities(
    @Query() { status, clientId, projectId }: GetActivitiesQueryParams
  ): Promise<Activity[]> {
    const activityRecords = await this.activitiesService.getActivities(
      status,
      clientId,
      projectId
    );

    const result: Activity[] = activityRecords.map((item) => ({
      id: item._id.toString(),
      name: item.name,
      status: item.status,
      clientId: item.client._id.toString(),
      projectId: item.project._id.toString(),
    }));

    return result;
  }

  @ApiBearerAuth()
  @ApiCreatedResponse({ description: 'Created' })
  @ApiBadRequestResponse({ description: 'Bad request' })
  @ApiUnauthorizedResponse({ description: 'Unauthorized' })
  @ApiForbiddenResponse({ description: "User's role is not 'admin'" })
  @ApiConflictResponse({
    description: 'Activity with same name already exists for project',
  })
  @ApiInternalServerErrorResponse({ description: 'Internal error' })
  @Post()
  public async createActivity(
    @Req() request: Request,
    @Body() createActivityDto: CreateActivityDto
  ): Promise<ICreatedIdDto> {
    await this.authService.ensureCurrentUserIsAdmin(request);

    const id = await this.activitiesService.createActivity(createActivityDto);
    return { id };
  }

  @ApiBearerAuth()
  @ApiNoContentResponse({ description: 'No content' })
  @ApiBadRequestResponse({ description: 'Bad request' })
  @ApiUnauthorizedResponse({ description: 'Unauthorized' })
  @ApiForbiddenResponse({ description: "User's role is not 'admin'" })
  @ApiNotFoundResponse({ description: 'Activity id not found' })
  @ApiConflictResponse({
    description: 'Activity with same name already exists for project',
  })
  @ApiInternalServerErrorResponse({ description: 'Internal error' })
  @ApiParam({ required: true, name: 'activityId' })
  @HttpCode(HttpStatus.NO_CONTENT)
  @Patch(':activityId')
  public async updateActivity(
    @Req() request: Request,
    @Param() { activityId }: UpdateActivityParams,
    @Body() updateActivityDto: UpdateActivityDto
  ): Promise<void> {
    await this.authService.ensureCurrentUserIsAdmin(request);

    await this.activitiesService.updateActivity(activityId, updateActivityDto);
  }

  @ApiBearerAuth()
  @ApiNoContentResponse({ description: 'No content' })
  @ApiUnauthorizedResponse({ description: 'Unauthorized' })
  @ApiForbiddenResponse({ description: "User's role is not 'admin'" })
  @ApiNotFoundResponse({ description: 'Activity id not found' })
  @ApiInternalServerErrorResponse({ description: 'Internal error' })
  @ApiParam({ required: true, name: 'activityId' })
  @HttpCode(HttpStatus.NO_CONTENT)
  @Delete(':activityId')
  public async deleteActivity(
    @Req() request: Request,
    @Param() { activityId }: DeleteActivityParams
  ): Promise<void> {
    await this.authService.ensureCurrentUserIsAdmin(request);

    await this.activitiesService.deleteActivity(activityId);
  }
}
