import {
  Body,
  Controller,
  Delete,
  Get,
  HttpCode,
  HttpStatus,
  Param,
  Patch,
  Post,
  Query,
  Req,
} from '@nestjs/common';
import {
  ApiBadRequestResponse,
  ApiBearerAuth,
  ApiConflictResponse,
  ApiCreatedResponse,
  ApiForbiddenResponse,
  ApiInternalServerErrorResponse,
  ApiNoContentResponse,
  ApiNotFoundResponse,
  ApiOkResponse,
  ApiParam,
  ApiTags,
  ApiUnauthorizedResponse,
} from '@nestjs/swagger';
import { ICreatedIdDto } from '@hrms/shared/dtos';
import { ClientsService } from './clients.service';
import { AuthService } from '../auth/auth.service';
import { CreateClientDto } from './dtos/create-client.dto';
import { Client } from './models/client';
import { GetClientsQueryParams } from './query-params/get-clients.query-params';
import { UpdateClientParams } from './params/update-client.params';
import { UpdateClientDto } from './dtos/update-client.dto';
import { DeleteClientParams } from './params/delete-client.params';

@ApiTags('Clients')
@Controller('clients')
export class ClientsController {
  constructor(
    private clientsService: ClientsService,
    private authService: AuthService
  ) {}

  @ApiBearerAuth()
  @ApiOkResponse({
    description: 'Success',
    type: Client,
    isArray: true,
  })
  @ApiBadRequestResponse({ description: 'Invalid status' })
  @ApiUnauthorizedResponse({ description: 'Unauthorized' })
  @ApiInternalServerErrorResponse({ description: 'Internal error' })
  @Get()
  public async getClients(
    @Query() { status }: GetClientsQueryParams
  ): Promise<Client[]> {
    const clientRecords = await this.clientsService.getClients(status);

    const result: Client[] = clientRecords.map((item) => ({
      id: item._id.toString(),
      name: item.name,
      status: item.status,
      contactName: item.contactName,
      contactEmail: item.contactEmail,
      contactPhone: item.contactPhone,
      contactMethod: item.contactMethod,
    }));

    return result;
  }

  @ApiBearerAuth()
  @ApiCreatedResponse({ description: 'Created' })
  @ApiBadRequestResponse({ description: 'Bad request' })
  @ApiUnauthorizedResponse({ description: 'Unauthorized' })
  @ApiForbiddenResponse({ description: "User's role is not 'admin'" })
  @ApiConflictResponse({ description: 'Client with same name already exists' })
  @ApiInternalServerErrorResponse({ description: 'Internal error' })
  @Post()
  public async createClient(
    @Req() request: Request,
    @Body() createClientDto: CreateClientDto
  ): Promise<ICreatedIdDto> {
    await this.authService.ensureCurrentUserIsAdmin(request);

    const id = await this.clientsService.createClient(createClientDto);
    return { id };
  }

  @ApiBearerAuth()
  @ApiNoContentResponse({ description: 'No content' })
  @ApiBadRequestResponse({ description: 'Bad request' })
  @ApiUnauthorizedResponse({ description: 'Unauthorized' })
  @ApiForbiddenResponse({ description: "User's role is not 'admin'" })
  @ApiNotFoundResponse({ description: 'Client id not found' })
  @ApiConflictResponse({ description: 'Client with same name already exists' })
  @ApiInternalServerErrorResponse({ description: 'Internal error' })
  @ApiParam({ required: true, name: 'clientId' })
  @HttpCode(HttpStatus.NO_CONTENT)
  @Patch(':clientId')
  public async updateClient(
    @Req() request: Request,
    @Param() { clientId }: UpdateClientParams,
    @Body() updateClientDto: UpdateClientDto
  ): Promise<void> {
    await this.authService.ensureCurrentUserIsAdmin(request);

    await this.clientsService.updateClient(clientId, updateClientDto);
  }

  @ApiBearerAuth()
  @ApiNoContentResponse({ description: 'No content' })
  @ApiUnauthorizedResponse({ description: 'Unauthorized' })
  @ApiForbiddenResponse({ description: "User's role is not 'admin'" })
  @ApiNotFoundResponse({ description: 'Client id not found' })
  @ApiInternalServerErrorResponse({ description: 'Internal error' })
  @ApiParam({ required: true, name: 'clientId' })
  @HttpCode(HttpStatus.NO_CONTENT)
  @Delete(':clientId')
  public async deleteClient(
    @Req() request: Request,
    @Param() { clientId }: DeleteClientParams
  ): Promise<void> {
    await this.authService.ensureCurrentUserIsAdmin(request);

    await this.clientsService.deleteClient(clientId);
  }
}
