import { ApiProperty } from '@nestjs/swagger';
import { IsIn, IsMongoId, ValidateIf } from 'class-validator';
import {
  TimesheetEventType,
  timesheetEventTypeList,
} from '@hrms/shared/models';
import { ICreateTimesheetEventDto } from '@hrms/shared/dtos';
import { IsDateOnlyString } from '../../../validators/is-date-only-string.validator';
import { IsDuration } from '../../../validators/is-duration.validator';

export class CreateTimesheetEventDto implements ICreateTimesheetEventDto {
  @ApiProperty({ example: new Date().toISOString().split('T')[0] })
  @IsDateOnlyString()
  public readonly date: string;

  @ApiProperty({ example: 30 })
  @IsDuration()
  public readonly durationMinutes: number;

  @ApiProperty({
    enum: timesheetEventTypeList,
    example: 'work',
  })
  @IsIn(timesheetEventTypeList)
  public readonly eventType: TimesheetEventType;

  @ApiProperty()
  @ValidateIf((o) => o.eventType === 'work')
  @IsMongoId()
  public readonly activityId?: string;

  @ApiProperty()
  public readonly comment?: string;
}
