import {
  Body,
  Controller,
  Delete,
  Get,
  HttpCode,
  HttpStatus,
  Param,
  Patch,
  Post,
  Query,
  Req,
} from '@nestjs/common';
import {
  ApiBadRequestResponse,
  ApiBearerAuth,
  ApiConflictResponse,
  ApiCreatedResponse,
  ApiForbiddenResponse,
  ApiInternalServerErrorResponse,
  ApiNoContentResponse,
  ApiNotFoundResponse,
  ApiOkResponse,
  ApiParam,
  ApiTags,
  ApiUnauthorizedResponse,
} from '@nestjs/swagger';
import { ProjectsService } from './projects.service';
import { Project } from './models/project';
import { GetProjectsQueryParams } from './query-params/get-projects.query-params';
import { ICreatedIdDto } from '@hrms/shared/dtos';
import { CreateProjectDto } from './dtos/create-project.dto';
import { UpdateProjectParams } from './params/update-project.params';
import { UpdateProjectDto } from './dtos/update-project.dto';
import { AuthService } from '../auth/auth.service';
import { DeleteProjectParams } from './params/delete-project.params';

@ApiTags('Projects')
@Controller('projects')
export class ProjectsController {
  constructor(
    private projectsService: ProjectsService,
    private authService: AuthService
  ) {}

  @ApiBearerAuth()
  @ApiOkResponse({
    description: 'Success',
    type: Project,
    isArray: true,
  })
  @ApiBadRequestResponse({
    description:
      'Invalid status \t\n Invalid client id format (not a MongoDb ObjectId)',
  })
  @ApiUnauthorizedResponse({ description: 'Unauthorized' })
  @ApiInternalServerErrorResponse({ description: 'Internal error' })
  @Get()
  public async getProjects(
    @Query() { status, clientId }: GetProjectsQueryParams
  ): Promise<Project[]> {
    const projectRecords = await this.projectsService.getProjects(
      status,
      clientId
    );

    const result: Project[] = projectRecords.map((item) => ({
      id: item._id.toString(),
      name: item.name,
      status: item.status,
      clientId: item.client._id.toString(),
    }));

    return result;
  }

  @ApiBearerAuth()
  @ApiCreatedResponse({ description: 'Created' })
  @ApiBadRequestResponse({ description: 'Bad request' })
  @ApiUnauthorizedResponse({ description: 'Unauthorized' })
  @ApiForbiddenResponse({ description: "User's role is not 'admin'" })
  @ApiConflictResponse({
    description: 'Project with same name already exists for client',
  })
  @ApiInternalServerErrorResponse({ description: 'Internal error' })
  @Post()
  public async createProject(
    @Req() request: Request,
    @Body() createProjectDto: CreateProjectDto
  ): Promise<ICreatedIdDto> {
    await this.authService.ensureCurrentUserIsAdmin(request);

    const id = await this.projectsService.createProject(createProjectDto);
    return { id };
  }

  @ApiBearerAuth()
  @ApiNoContentResponse({ description: 'No content' })
  @ApiBadRequestResponse({ description: 'Bad request' })
  @ApiUnauthorizedResponse({ description: 'Unauthorized' })
  @ApiForbiddenResponse({ description: "User's role is not 'admin'" })
  @ApiNotFoundResponse({ description: 'Project id not found' })
  @ApiConflictResponse({
    description: 'Project with same name already exists for client',
  })
  @ApiInternalServerErrorResponse({ description: 'Internal error' })
  @ApiParam({ required: true, name: 'projectId' })
  @HttpCode(HttpStatus.NO_CONTENT)
  @Patch(':projectId')
  public async updateProject(
    @Req() request: Request,
    @Param() { projectId }: UpdateProjectParams,
    @Body() updateProjectDto: UpdateProjectDto
  ): Promise<void> {
    await this.authService.ensureCurrentUserIsAdmin(request);

    await this.projectsService.updateProject(projectId, updateProjectDto);
  }

  @ApiBearerAuth()
  @ApiNoContentResponse({ description: 'No content' })
  @ApiUnauthorizedResponse({ description: 'Unauthorized' })
  @ApiForbiddenResponse({ description: "User's role is not 'admin'" })
  @ApiNotFoundResponse({ description: 'Project id not found' })
  @ApiInternalServerErrorResponse({ description: 'Internal error' })
  @ApiParam({ required: true, name: 'projectId' })
  @HttpCode(HttpStatus.NO_CONTENT)
  @Delete(':projectId')
  public async deleteProject(
    @Req() request: Request,
    @Param() { projectId }: DeleteProjectParams
  ): Promise<void> {
    await this.authService.ensureCurrentUserIsAdmin(request);

    await this.projectsService.deleteProject(projectId);
  }
}
