import { ApiProperty } from '@nestjs/swagger';
import { IsEmail, IsIn, IsNumberString, IsOptional } from 'class-validator';
import {
  ClientContactMethod,
  ClientStatus,
  clientContactMethodList,
  clientStatusList,
} from '@hrms/shared/models';
import { ICreateClientDto } from '@hrms/shared/dtos';
import { IsNotBlankString } from '../../../validators/is-not-blank-string.validator';

export class CreateClientDto implements ICreateClientDto {
  @ApiProperty({ example: 'Microsoft' })
  @IsNotBlankString()
  public readonly name: string;

  @ApiProperty({
    enum: clientStatusList,
    example: 'active',
  })
  @IsIn(clientStatusList)
  public readonly status: ClientStatus;

  @ApiProperty({ example: 'John Smith' })
  @IsOptional()
  @IsNotBlankString()
  public readonly contactName?: string;

  @ApiProperty({ example: 'john.smith@client.com' })
  @IsOptional()
  @IsEmail()
  public readonly contactEmail?: string;

  @ApiProperty({ example: '16042223333' })
  @IsOptional()
  @IsNumberString()
  public readonly contactPhone?: string;

  @ApiProperty({
    enum: clientContactMethodList,
    example: 'email',
    required: false,
  })
  @IsOptional()
  @IsIn(clientContactMethodList)
  public readonly contactMethod?: ClientContactMethod;
}
