import { UserRecord } from '../../users/schemas/user.schema';
import { BadRequestException } from '@nestjs/common';
import { UserRecordHelper } from '../../users/helpers/user-record.helper';
import { ProjectRecord } from '../../projects/schemas/project.schema';

export const validateProjectId = (
  userRecord: UserRecord,
  userRecordHelper: UserRecordHelper,
  projectId: string | undefined
): ProjectRecord | undefined => {
  const assignedProject = projectId
    ? userRecordHelper.getAssignedProject(userRecord, projectId)
    : undefined;

  if (!assignedProject) {
    throw new BadRequestException(
      `projectId '${projectId}' not available for user '${userRecord._id}'`
    );
  }

  return assignedProject;
};
