import { ApiProperty } from '@nestjs/swagger';
import { ProjectStatus, projectStatusList } from '@hrms/shared/models';
import { IsIn, IsMongoId, IsOptional } from 'class-validator';

export class GetProjectsQueryParams {
  @ApiProperty({
    example: 'active',
    enum: projectStatusList,
    required: false,
  })
  @IsOptional()
  @IsIn(projectStatusList)
  public status: ProjectStatus | undefined;

  @ApiProperty({
    type: String,
    required: false,
  })
  @IsOptional()
  @IsMongoId()
  public clientId: string | undefined;
}
