import { ApiProperty } from '@nestjs/swagger';
import { IsIn, IsOptional } from 'class-validator';
import { IsNotBlankString } from '../../../validators/is-not-blank-string.validator';
import { ActivityStatus, activityStatusList } from '@hrms/shared/models';
import { IUpdateActivityDto } from '@hrms/shared/dtos';

export class UpdateActivityDto implements IUpdateActivityDto {
  @ApiProperty({ example: 'Meeting' })
  @IsOptional()
  @IsNotBlankString()
  public readonly name?: string;

  @ApiProperty({
    enum: activityStatusList,
    example: 'enabled',
    required: false,
  })
  @IsOptional()
  @IsIn(activityStatusList)
  public readonly status?: ActivityStatus;
}
