import { Injectable } from '@nestjs/common';
import { TimesheetEventRecord } from './schemas/timesheet-event.schema';
import {
  TimesheetEvent,
  TimesheetEventContext,
} from './models/timesheet-event';

@Injectable()
export class TimesheetEventsAssembler {
  public assembleEvents(
    timesheetEventRecords: TimesheetEventRecord[]
  ): TimesheetEvent[] {
    return timesheetEventRecords.map((eventRecord) => {
      const context: TimesheetEventContext | undefined = eventRecord.activity
        ? {
            activityId: eventRecord.activity._id.toString(),
            projectId: eventRecord.project?._id.toString(),
            clientId: eventRecord.client?._id.toString(),
          }
        : undefined;

      return {
        id: eventRecord._id.toString(),
        userId: eventRecord.user._id.toString(),
        date: eventRecord.date,
        eventType: eventRecord.eventType,
        durationMinutes: eventRecord.durationMinutes,
        comment: eventRecord.comment,
        context,
      };
    });
  }
}
