import { Injectable } from '@nestjs/common';
import { UserRecord } from '../schemas/user.schema';
import { ProjectRecord } from '../../projects/schemas/project.schema';
import { ActivityRecord } from '../../activities/schemas/activity.schema';

@Injectable()
export class UserRecordHelper {
  public getAssignedActivity(
    userRecord: UserRecord,
    activityId: string
  ): ActivityRecord | undefined {
    const assignedActivities = userRecord.activities;

    return (
      assignedActivities.find((activity) => {
        return activity._id.equals(activityId);
      }) ?? undefined
    );
  }

  public getAssignedProject(
    userRecord: UserRecord,
    projectId: string
  ): ProjectRecord | undefined {
    const assignedProjects = userRecord.projects;

    return (
      assignedProjects.find((project) => {
        return project._id.equals(projectId);
      }) ?? undefined
    );
  }
}
