import { Schema, Prop, SchemaFactory } from '@nestjs/mongoose';
import { HydratedDocument, SchemaTypes, Types } from 'mongoose';
import * as bcrypt from 'bcrypt';

import { UserRole, UserStatus } from '@hrms/shared/models';
import { ActivityRecord } from '../../activities/schemas/activity.schema';
import { ProjectRecord } from '../../projects/schemas/project.schema';
import { ClientRecord } from '../../clients/schemas/client.schema';

export type UserDocument = HydratedDocument<UserRecord>;

@Schema({ collection: 'users' })
export class UserRecord {
  public _id: Types.ObjectId;

  @Prop({ required: true })
  public firstName: string;

  @Prop({ required: true })
  public lastName: string;

  @Prop({ required: true })
  public email: string;

  @Prop({ required: true, default: 'member', type: String })
  public role: UserRole;

  @Prop({ required: true, default: 'enabled', type: String })
  public status: UserStatus;

  @Prop()
  public passwordHash: string;

  @Prop([{ type: SchemaTypes.ObjectId, ref: ClientRecord.name }])
  public clients: ClientRecord[];

  @Prop([{ type: SchemaTypes.ObjectId, ref: ProjectRecord.name }])
  public projects: ProjectRecord[];

  @Prop([{ type: SchemaTypes.ObjectId, ref: ActivityRecord.name }])
  public activities: ActivityRecord[];
}

const UserSchema = SchemaFactory.createForClass(UserRecord);

UserSchema.pre('save', async function (next) {
  if (!this.isModified('password')) return next();
  this.passwordHash = await bcrypt.hash(this.passwordHash, 10);
  next();
});

export { UserSchema };
