export const clientStatusList = ['active', 'inactive'] as const;
export const clientContactMethodList = ['email', 'phone'] as const;

export type ClientStatus = (typeof clientStatusList)[number];
export type ClientContactMethod = (typeof clientContactMethodList)[number];

export interface IClient {
  id: string;
  name: string;
  status: ClientStatus;
  contactName?: string;
  contactEmail?: string;
  contactPhone?: string;
  contactMethod?: ClientContactMethod;
}
