import { Model, Types } from 'mongoose';
import { BadRequestException, NotFoundException } from '@nestjs/common';
import {
  WorkSummaryDocument,
  WorkSummaryRecord,
} from '../../work-summaries/schemas/work-summary.schema';

export const validateWorkSummaryId = async (
  workSummaryModel: Model<WorkSummaryRecord>,
  userId: Types.ObjectId,
  workSummaryId: string
): Promise<WorkSummaryDocument> => {
  const workSummary = await workSummaryModel.findById(workSummaryId).exec();

  if (!workSummary) {
    throw new NotFoundException(
      `Could not find work summary for '${workSummaryId}'`
    );
  }

  if (!workSummary.user._id.equals(userId)) {
    throw new BadRequestException(
      `Work summary '${workSummaryId}' does not belong to user '${userId}'`
    );
  }

  return workSummary;
};
