import { IsMongoId } from 'class-validator';
import { IUpdateUserActivitiesDto } from '@hrms/shared/dtos';
import { ApiProperty } from '@nestjs/swagger';

export class UpdateUserActivitiesDto implements IUpdateUserActivitiesDto {
  @ApiProperty({ required: true })
  @IsMongoId()
  public readonly projectId: string;

  @ApiProperty({ required: false, isArray: true, example: [] })
  @IsMongoId({ each: true })
  public readonly activityIds?: string[];
}
