import {
  BadRequestException,
  ConflictException,
  Injectable,
  InternalServerErrorException,
  NotFoundException,
} from '@nestjs/common';
import { InjectModel } from '@nestjs/mongoose';
import { Model } from 'mongoose';
import { ClientStatus } from '@hrms/shared/models';
import { ClientRecord } from './schemas/client.schema';
import { CreateClientDto } from './dtos/create-client.dto';
import { UpdateClientDto } from './dtos/update-client.dto';
import { ActivityRecord } from '../activities/schemas/activity.schema';
import { ProjectRecord } from '../projects/schemas/project.schema';
import { TimesheetEventRecord } from '../timesheet-events/schemas/timesheet-event.schema';
import { UserRecord } from '../users/schemas/user.schema';

@Injectable()
export class ClientsService {
  constructor(
    @InjectModel(ClientRecord.name)
    private clientModel: Model<ClientRecord>,
    @InjectModel(ActivityRecord.name)
    private activityModel: Model<ActivityRecord>,
    @InjectModel(ProjectRecord.name)
    private projectModel: Model<ProjectRecord>,
    @InjectModel(TimesheetEventRecord.name)
    private timesheetEventModel: Model<TimesheetEventRecord>,
    @InjectModel(UserRecord.name)
    private userModel: Model<UserRecord>
  ) {}

  public async findById(id: string): Promise<ClientRecord | undefined> {
    const result = await this.clientModel.findById(id).exec();

    return result ?? undefined;
  }

  public async getClients(status?: ClientStatus): Promise<ClientRecord[]> {
    if (status) {
      return await this.clientModel.find({ status }).exec();
    }

    return await this.clientModel.find().exec();
  }

  public async createClient(createClientDto: CreateClientDto): Promise<string> {
    const {
      name,
      status,
      contactName,
      contactEmail,
      contactPhone,
      contactMethod,
    } = createClientDto;

    const existingClientRecord = await this.findClientByName(name);
    if (existingClientRecord) {
      throw new ConflictException('Client with same name already exists');
    }

    const clientModel = new this.clientModel({
      name: name.trim(),
      status,
      contactName,
      contactEmail,
      contactPhone,
      contactMethod,
    });

    const clientId = (await clientModel.save())?.id;
    if (!clientId) {
      throw new InternalServerErrorException('Failed to save client');
    }

    return clientId;
  }

  public async updateClient(
    clientId: string,
    updateClientDto: UpdateClientDto
  ): Promise<void> {
    const {
      name,
      status,
      contactName,
      contactEmail,
      contactPhone,
      contactMethod,
    } = updateClientDto;

    const targetClient = await this.clientModel.findById(clientId);
    if (!targetClient) {
      throw new NotFoundException(`Client id not found`);
    }

    if (name) {
      const duplicateClient = await this.findClientByName(name);

      if (duplicateClient && duplicateClient._id.toString() !== clientId) {
        throw new ConflictException('Client with same name already exists');
      }
    }

    const setFields: Record<string, string> = {};
    const unsetFields: Record<string, string> = {};

    if (name) {
      setFields.name = name.trim();
    }
    if (status) {
      setFields.status = status;
    }
    if (contactMethod) {
      setFields.contactMethod = contactMethod;
    }

    if (contactName) {
      setFields.contactName = contactName;
    } else if (contactName === null) {
      unsetFields.contactName = '';
    }

    if (contactEmail) {
      setFields.contactEmail = contactEmail;
    } else if (contactEmail === null) {
      unsetFields.contactEmail = '';
    }

    if (contactPhone) {
      setFields.contactPhone = contactPhone;
    } else if (contactPhone === null) {
      unsetFields.contactPhone = '';
    }

    await targetClient.updateOne({
      $set: setFields,
      $unset: unsetFields,
    });
  }

  public async deleteClient(clientId: string): Promise<void> {
    const targetClient = await this.clientModel.findById(clientId);

    if (!targetClient) {
      throw new NotFoundException('Client id not found');
    }

    const assignedUser = await this.userModel.findOne({
      clients: clientId,
    });

    if (assignedUser) {
      throw new BadRequestException('Cannot delete client assigned to user(s)');
    }

    const childActivities = await this.activityModel.find({
      client: clientId,
    });

    for (const activity of childActivities) {
      const activityId = activity._id.toString();

      const timesheetEvent = await this.timesheetEventModel.findOne({
        activity: activityId,
      });

      if (timesheetEvent) {
        throw new BadRequestException(
          'Cannot delete client with any activity referenced in timesheet event(s)'
        );
      }
    }

    await this.activityModel.deleteMany({
      client: clientId,
    });
    await this.projectModel.deleteMany({
      client: clientId,
    });
    await targetClient.deleteOne();
  }

  private findClientByName(name: string): Promise<ClientRecord | null> {
    const clientNameRegex = '^' + name.trim() + '$';

    return this.clientModel.findOne({
      name: { $regex: clientNameRegex, $options: 'i' },
    });
  }
}
