import { Injectable } from '@nestjs/common';
import { ConfigService } from '@nestjs/config';
import { IConfiguration } from '../../models/configuration';
import { UserRecord } from './schemas/user.schema';
import {
  AssignedActivity,
  AssignedClient,
  AssignedProject,
  User,
  UserRole,
  UserStatus,
} from '@hrms/shared/models';
import { ClientRecord } from '../clients/schemas/client.schema';
import { ProjectRecord } from '../projects/schemas/project.schema';
import { ActivityRecord } from '../activities/schemas/activity.schema';

@Injectable()
export class UsersAssembler {
  constructor(private configService: ConfigService<IConfiguration, true>) {}

  public assembleUser(userRecord: UserRecord): User {
    return {
      id: userRecord._id.toString(),
      firstName: userRecord.firstName,
      lastName: userRecord.lastName,
      email: userRecord.email,
      role: userRecord.role as UserRole,
      status: userRecord.status as UserStatus,
      organization: this.configService.get('organization', { infer: true }),
      clients: this.assembleAssignedClients(userRecord.clients),
      projects: this.assembleAssignedProjects(userRecord.projects),
      activities: this.assembleAssignedActivities(userRecord.activities),
    };
  }

  private assembleAssignedClients(
    assignedClientRecords: ClientRecord[] = []
  ): AssignedClient[] {
    return assignedClientRecords.map((clientRecord) => ({
      id: clientRecord._id.toString(),
      name: clientRecord.name,
    }));
  }

  private assembleAssignedProjects(
    assignedProjectRecords: ProjectRecord[] = []
  ): AssignedProject[] {
    return assignedProjectRecords.map((projectRecord) => ({
      id: projectRecord._id.toString(),
      name: projectRecord.name,
    }));
  }

  private assembleAssignedActivities(
    assignedActivityRecords: ActivityRecord[] = []
  ): AssignedActivity[] {
    return assignedActivityRecords.map((activityRecord) => ({
      id: activityRecord._id.toString(),
      name: activityRecord.name,
    }));
  }
}
